.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header_top{
  display:flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  width:50%;
  margin:0 auto;
}

::placeholder{
  font-size: medium;
}
.header_title{
  width:50%;
  margin:0 auto;
  border:2px solid #ccc;
  border-radius:6px;
  padding:5px 0;
}

.TimeDate_form{
  display:flex;
  justify-content:space-around;
  margin:0 auto;
}

.centerMe{
  margin:5px 0;
  font-weight:bold;
}

.icon_progress{
  width:50%;
  display:flex;
  justify-content:center;
  align-items:center;
  margin:5px auto;
  background:#ccc;
  padding:10px 5px;
  border-radius:6px;
}

.FormField{
  width:50%;
  margin:10px auto 0;
  border:2px solid #ccc;
  border-radius:6px;
  padding:5px 0;
}

.formControl{
  display:flex;
  justify-content:left;
  padding:0 10px;
  margin:8px 5px;
  border-bottom:2px solid green;
  width:40%;
}

.btn_button{
  width:30%;
  margin:15px auto;
}

.childFooter{
  display:flex;
  justify-content:space-between;
  align-items:center;
  width:65%; 
  margin:0 auto;
}


@media only screen and (max-width: 770px){
  body{
    margin:0;
    padding:0;
    box-sizing: border-box;
    width:100%;
  }
  .header_top{
    width:100%;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .header_title{
    width:95%;
  }
  .TimeDate_form{
    display:flex;
    flex-direction: column;
    justify-content:space-around;
    margin:0 auto;
  }
  .TimeDate_form p{
    font-size:14px;
  }
  .TimeDate_form small{
    font-size:11px;
  }
  .centerMe{
    text-align: center;
    width:80%;
    margin:0 auto;
  }
  .icon_progress{
    width:90%;
    display:flex;
    justify-content:center;
    align-items:center;
    margin:5px auto;
    background:#ccc;
    padding:10px 5px;
    border-radius:6px;
  }

  .FormField{
    width:90%;
    margin:10px auto 0;
    border:2px solid #ccc;
    border-radius:6px;
    padding:5px 0;
  }

  .formControl{
    display:flex;
    justify-content:left;
    padding:0 10px;
    margin:8px 5px;
    border-bottom:2px solid green;
    width:80%;
  }

  .btn_button{
    width:90%;
    margin:15px auto;
  }

  footer{
    width:100%;
    overflow:hidden;
  }
  .childFooter{
    flex-wrap:wrap;
    justify-content: center;
    width:100%;
  }
}